body{
  background-color: #333;
}
.contenedor{
  display:flex;
  margin:30px auto;
  justify-content: center;
}

.titulo{
  height: 180px;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  color:#fff;
}

.esta-sonando{
  height: 50px;
}
.nota{
  background: #fff;
  margin:5px;
  border-radius: 5px;
  height: 400px;
  width:100px;
  position: relative;
}
.suena{
  transition: 0.5s;
  background-color: #999;
}

.negra{
  background-color: #111;
  height: 250px;
  width:50px;
  border-radius: 10px;
  position: absolute;
  right: -30px;
  z-index: 9;
}

.estaSonando{
  background-color: #999;
  transition: 0.5s;
}
